import uniRate from "../../components/uni-ui/uni-rate/uni-rate.vue"
export default {
	components: {
		uniRate
	},
	data() {
		return {
			itemDetailsData: {},
			disabledBle: true,
			actorsData: [], //演职人员
			directorData: [], //导演
			stillData: [], //剧照
			showPreviewImage: false
		}
	},
	//监听页面隐藏
	onHide: function() {

	},
	//用户点击右上角分享
	 onShareAppMessage: function () {
	     return {
			 title: this.itemDetailsData.name,
			 path: `/pages/details/details`,
			 success: (res) => {
				 console.log(res);
				 console.log(222);
			 },
			 fail: (err) => {
				 console.log(err);
			 }
		 }
	 },
	onLoad(options) {
		this.itemDetailsData = JSON.parse(options.itemId)
		this.stillData = JSON.parse(this.itemDetailsData.plotPics)
		console.log(this.itemDetailsData);
		console.log(this.stillData);
		this.getTrailerData()
		this.getDirectorData()
	},
	methods: {
		playObj() {

		},
		//获取演职人员
		getTrailerData() {
			uni.request({
				url: `${this.$api}/search/staff/${this.itemDetailsData.id}/2?qq=434714873`,
				method: 'POST',
				data: {},
				success: res => {
					this.actorsData = res.data.data
					// console.log(this.actorsData);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取导演
		getDirectorData() {
			uni.request({
				url: `${this.$api}/search/staff/${this.itemDetailsData.id}/1?qq=434714873`,
				method: 'POST',
				data: {},
				success: res => {
					this.directorData = res.data.data
					console.log(this.directorData);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//返回
		returnObj() {
			uni.switchTab({
				url: `../home/home`
			})
		},
		//图片预览
		previewImage() {
			this.showPreviewImage = true
		},
		//取消图片预览
		nonePreviewImage() {
			this.showPreviewImage = false
		},
		//长按图片事件
		longpress() {
			uni.showActionSheet({
				itemList: ['保存到本地图片'],
				success: res => {
					uni.downloadFile({
						url: this.itemDetailsData.poster,
						success(res) {
							console.log(res);
							if (res.statusCode === 200) {
								uni.saveImageToPhotosAlbum({
									filePath: res.tempFilePath,
									success: res => {
										console.log(res)
										uni.showToast({
											title: '保存成功'
										})
									},
									fail: err => {
										console.log(err);
										uni.showToast({
											title: '保存失败，请稍后重试',
											icon: 'none'
										})
									}
								})
							}
						}
					})
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		}
	}
}
